<template>
	<view>
		<view class="demo" :style="'height:' +  demo.height + 'px;' + 'padding-top:' + demo.top + 'px;padding-bottom:10rpx'">
		    <view class="left" :style="'top:' + demo.top + 'px'">
		        <view class="iconfont icon-zuo" @tap='backleft'></view>
		    </view>
		    <view class="clr_fff">{{goodsdtail.title}}</view>
		</view>
		<view class="pd_t123">
			<image class="img100 block" mode="widthFix"  :src="goodsdtail.logo"></image>
		</view>
		<view class="uni-padding-wrap backWhite">
			<view class="uni-flex" style="justify-content:space-between;">
				<view class="title">{{goodsdtail.title}}</view>
				<view>
					<view><span class="thicon">现特惠</span> <span class="price">￥{{goodsdtail.list[0].price_selling}}/{{goodsdtail.unit}}</span>  </view>
					<view class="originalPrice text-right">￥{{goodsdtail.list[0].price_market}}/{{goodsdtail.unit}}</view>
				</view>
			</view>			
		</view>
		<view class="bzview">
			<view class="uni-padding-wrap">
				<view class="uni-flex">
					<view class="flex-item">
						<image class="img100" mode="widthFix" src="https://test134.vrapp.chot.cn/mini/picture/bz-tuihuan.png"></image>				
					</view>
					<view class="flex-item">
						<image class="img100" mode="widthFix" src="https://test134.vrapp.chot.cn/mini/picture/bz-fwu.png"></image>
					</view>
					<view class="flex-item">
						<image class="img100" mode="widthFix" src="https://test134.vrapp.chot.cn/mini/picture/bz-baogao.png"></image>
					</view>
				</view>
			</view>
		</view>
		<view class="dtxq">
			<view class="uni-padding-wrap">
				<view>					
					<rich-text :nodes="goodsdtail.content"></rich-text>					
				</view>
			</view>
		</view>
		<view class="goodsDtfooter">
			<view class="uni-flex uni-padding-wrap mrtb30">
				<view class="w186">
					<view class="uni-center price" :class="[btnspec ? 'show' : 'hide' ]">￥{{goodsdtail.list[0].price_selling}}</view>
					<view class="uni-center price" :class="[btnspec ? 'hide' : 'show' ]">￥{{totalPrice.shopPrice * num}}</view>
					<view class="uni-center originalPrice" :class="[btnspec ? 'show' : 'hide' ]">￥{{goodsdtail.list[0].price_market}}</view>
					<view class="uni-center originalPrice" :class="[btnspec ? 'hide' : 'show' ]">￥{{totalPrice.marketPrice * num}}</view>
				</view>
				<view class="w77">
					<image @tap="callPhoneTap" class="w55" src="https://test134.vrapp.chot.cn/mini/picture/kehu.png"></image>
				</view>
				<view class="uni-flex">
					<button :class="[btnspec ? 'show' : 'hide' ]" class="subscribe_btn" @tap="subscribe_btn">预约下单</button>
					<!--" @tap="submit_btn"  -->
					<button :class="[btnspec ? 'hide' : 'show' ]" class="subscribe_btn" @tap="choice_workers">选择护工</button>
				</view>
			</view>
		</view>		
		<view class="zhezhao" v-show="!fold">
			<scroll-view class="odr_module">
				<view class="cont_t">
					<view class="uni-flex top_T">
						<view class="md_title">选择套餐   </view>    
						<view @tap="spec_close"> <image class="w28" src="https://test134.vrapp.chot.cn/mini/picture/close.png" mode=""></image> </view>  
					</view>
					<view class="span-tit"> 
					 <image class="w21" src="https://test134.vrapp.chot.cn/mini/picture/dot-title.png" mode=""></image> 参数规格
					</view>
					<!-- <view class="spec">
						<text class="active">初级</text><text>初级</text>
					</view> -->
					<view class="specs" v-for="(item,index)  in specs" :key="index"> 
						<view>{{item.name}}:</view>
						<view class="spec" v-for="(item1,index1) in item.list" :key="index1" @click="orderChange(index,index1)">  
					<!-- 	{{States[index]}}{{index}} -->
							<text :class="[States[index]==index1 ? 'activeClass' : 'errorClass']" > {{item1.name}}</text>
						</view>	
					</view>
					<view class="span-tit">
						 <image class="w21" src="https://test134.vrapp.chot.cn/mini/picture/dot-title.png" mode="">  开始时间
					</view>
					<view class="date_btn">
						<button class="" type="button" @click="onShowDatePicker('datetime')">{{datetime}}</button>
						<mx-date-picker :show="showPicker" :type="type" :value="value" :show-tips="true" :begin-text="'入住'" :end-text="'离店'" :show-seconds="true" @confirm="onSelected" @cancel="onSelected" />
					</view>
					<view class="span-tit uni-flex" style="justify-content:space-between;">
						<view>
							<image class="w21" src="https://test134.vrapp.chot.cn/mini/picture/dot-title.png" mode="">  服务天数 <!-- <text class="clr23c0ba">(限购12个月)</text> -->   
						</view> 
						 <view class="stepper">
							<text @tap="bindMinus" :class="[isminNormal ? 'normal' : 'disabled' ]">-</text> <input type="number" @change="bindManual" v-model="num" /> <text @tap="bindPlus" :class="[ismaxNormal ? 'normal' : 'disabled' ]">+</text> 
						 </view>						 
					</view>
				</view>
			</scroll-view>
			
			
		</view>
		
		<!-- #ifdef MP-WEIXIN -->
		<view :class="[isCanUse ? 'show' : 'hide' ]">
			<view class="zhezhao">
				<view class="center">
					<view class="toastqx">
						<icon type="info" size="40" color='item'></icon>
						<view class='padd'>小程序需要你的微信授权才可以使用</view>
						<button class='testbutton' open-type="getUserInfo" @getuserinfo="getUserinfo" withCredentials="true">登录</button>
					</view>					
				</view>
			</view>						
		</view>				
		<!-- #endif -->	
		<view class="zhezhao"  :class="[isphoneView ? 'show' : 'hide' ]">
			<view class="center">
				<view class="toastqx">
					<icon type="info" size="40" color='item'></icon>
					<view class='padd'>小程序需要你的微信手机号授权</view>
					<button class='testbutton' open-type="getPhoneNumber"@getphonenumber="onGetPhoneNumber" withCredentials="true">手机号授权</button>
				</view>					
			</view>
		</view>	
		
		
		
		
		
	</view>
</template>

<script>
	import MxDatePicker from "../../components/mx-datepicker/mx-datepicker.vue";
	import uniIcons from "../../components/uni-icons/uni-icons.vue";
	import {updateUserInfo} from "../../common/updataInfo.js"
	import {formatDate} from '../../common/util.js'
	import app from "../../App.vue"
	var mydate = new Date();
	var mytime =mydate.getFullYear()+'-'+ (mydate.getMonth()+1) +'-'+mydate.getDate();
	
	export default {
		name: 'deTail',
		components: {
			MxDatePicker,
			uniIcons
		},
		
		created () {
			const demo = uni.getMenuButtonBoundingClientRect()
			this.demo.top = demo.top
			this.demo.height = demo.height
			
			uni.setNavigationBarColor({
				frontColor: '#000000',
				backgroundColor: '#000000'
			})
		},
		props: {
			activeColor: {
				// 星星选中状态颜色
				type: String,
				default: "#ffca3e"
			},
			size: {
				// 星星的大小
				type: [Number, String],
				default: 14
			}
		},
		data(){
			return{
				token:"",
				phone:'',
				nickName:"",
				avatarUrl:"",
				from_mid:0,
				isphoneView:false,
				current: 0,
				fold:true,
				btnspec:true,
				demo: {
					top: 0,
					height: 0
				},
				goods_id:"",
				goodsdtail:[],//商品详情数据
				showPicker:false,//时间状态
				datetime: mytime,
				type:'rangetime',//时间插件类型
				value:'',//时间插件
				num:1,//下单数量默认是1
				isminNormal:false,
				ismaxNormal:true,
				goodsInfo:{},
				
				totalPrice:{
					"marketPrice":0,
					"shopPrice":0
				},

				orderParam:{},
				States:{},
				specs:[],
				specList:[],
				goods_spec:"",	
				
			}
		},
		onLoad: function (option) {
			var goods_id= option.id;
			this.detail(goods_id,this.token);
			this.goods_id=goods_id
			this.login();
		},
		methods: {	
			// 获取微信用户信息
			getUserinfo: function(){				
				 var that=this;
				 uni.getUserInfo({
					success: function(res) {
						var userInfo = res.userInfo
						that.nickName = userInfo.nickName    //昵称
						that.avatarUrl = userInfo.avatarUrl //头像
						var gender = userInfo.gender //性别 0：未知、1：男、2：女
						var province = userInfo.province
						var city = userInfo.city
						var country = userInfo.country														
						that.isphoneView = true
						that.isCanUse=false; //记录是否第一次授权  false:表示不是第一次授权
	                    app.updateUserInfo();
						
					},
					fail:res=>{
					 uni.showToast({ 
						 title: '获取微信信息失败', 
						 duration: 2000 ,
						 icon:"none"
						 })						  
					}
				 })
			},
			login(){
					uni.showLoading({
						title: '加载中...',
					});
					// 1.wx获取登录用户code
					var _this = this;	
								
					app.wxLogin().then(res => {
							if(uni.getStorageSync('from_mid')==''){
								_this.from_mid=0;
							}else{
								_this.from_mid = uni.getStorageSync('from_mid');
							}
							_this.token = app.globalData.token;															
						   if(app.globalData.nickName==""){
								_this.nickName = uni.getStorageSync('nickName');
						   }else{			
								_this.nickName = app.globalData.nickName;					
								_this.phone = app.globalData.phone;	
								_this.avatarUrl = app.globalData.headimg;						
								if(_this.nickName!==""){
									_this.isCanUse=false
									app.updateUserInfo();
								} else{
									_this.isCanUse=true
									app.updateUserInfo();
								} 					 
								             			
				                  uni.hideLoading();						
												
												
												
						   }					   
					});			
								
									
				   
	        },
				
			onGetPhoneNumber(e){
				const session_key = uni.getStorageSync('session_key');
				if (e.detail.errMsg == 'getPhoneNumber:ok') {
					this.isphoneView = false;
				} else {
			 
				}				
				let encryptedData = e.detail.encryptedData;
				let iv=e.detail.iv;
				
				uni.request({
					url:"https://test134.vrapp.chot.cn/api",
					method:"POST",
					data:{
						operate:'Wechat.get_phone',
						session_key:session_key,
						encryptedData:encryptedData,
						iv:iv
						
					},
					success: (res) => {				
						if(res.data.code==1){
							let jsonData =res.data.data;
							let phoneData = JSON.parse(jsonData);												
							this.phone = phoneData.phoneNumber;	
							updateUserInfo(phoneData.phoneNumber,this.token);	//手机号	 调用updataInfo.js											
							
						}else{
							uni.showToast({
								title: '手机号授权失败',
								duration: 1000,
								icon:"none"
							});
							this.isphoneView=false;
						}
						
						
						
					}
				})
				
			
			},	
			radioChange: function(evt) {
				for (let i = 0; i < this.items.length; i++) {
					if (this.items[i].value === evt.target.value) {
						this.current = i;
						break;
					}
				}
			},
			orderChange(index,index1){
				this.States[index] = index1;
				//console.log("ss"+this.States[index]);
				console.log([index,index1]);
				
				console.log(this.specs[index].list[index1].name);
				
				this.build_order_data();
				// this.isCheckState =index
				// this.isCheckStates=index1
				
			},
			async callPhoneTap(){				
				const res = await this.$myRequset({
					url:"/api",
					data:{
						operate:"index.store_phone",
						token:this.token
					}					
				})
				uni.makePhoneCall({
					phoneNumber:res.data.data.phone,
					success: (res) => {
						console.log("拨打成功");
					},fail: (res) => {
						console.log("拨打失败");
					}
				})
				
			},
			build_order_data() {
				
				let goods_spec = this.build_spec_key();
				this.orderParam['token'] = this.token;
				this.orderParam['rule'] = this.goodsInfo.id + '@' + goods_spec + '@' + this.num;
				this.orderParam['from_mid'] = this.from_mid;
				this.orderParam['from_token'] = '';
				this.orderParam['order_type'] = 2;
				this.orderParam['nurse_id'] = 1;
				this.orderParam['caregivers_id'] = 1;
				this.orderParam['start_time'] = this.datetime;
				
				console.log(this.orderParam)		
				this.specList.forEach((item,index) => {
					if (item.goods_spec == goods_spec) {
						this.totalPrice.marketPrice = item['price_market'];
						this.totalPrice.shopPrice = item['price_selling'];
						
					}
				});
		
				
			},
			build_spec_key(){
				let str = '';
				let count = this.specs.length;

				this.specs.forEach((item,index)=>{
					str += item.name + '::' + item.list[this.States[index]]['name'];
					if (index + 1 != count) {
						str += ';;';
					}
				});

				return str;
			},
			
			backleft(e){
			  	uni.navigateBack();
			},
			choice_workers(){
				let goods_spec = this.build_spec_key();
				let rule = this.goodsInfo.id + '@' + goods_spec + '@' + this.num;
				let from_mid = this.from_mid;
				let from_token = '';
				let order_type = 2;
				let start_time = this.datetime;
				let goods_id = this.goods_id
				let token = this.token;
				uni.navigateTo({
					url: "../specs/specs?token="+token+"&rule="+rule+"&start_time="+start_time+"&goods_id="+goods_id+"&goods_spec="+goods_spec+"&from_mid="+from_mid
				});	
			},			
			subscribe_btn(e){				
				this.getphoneAPI();												
			},
			async getphoneAPI(){
				const res = await this.$myRequset({
					url:"/api",
					method:"POST",
					data:{
						operate:"MemberCenter.get_phone",
						token:this.token
					}					
				})				
				//console.log("电话号码:"+res.data.data)				
				if(res.data.data==""||res.data.data==null||res.data.data==undefined){
					this.isphoneView=true							
				}else{
					this.isphoneView=false;
					this.fold=false;
					this.btnspec=false;					
					let goods_spec = this.build_spec_key();
					this.specList.forEach((item,index) => {
						if (item.goods_spec == goods_spec) {
							this.totalPrice.marketPrice = item['price_market'];
							this.totalPrice.shopPrice = item['price_selling'];
						}
					});
				}
				
				
			},				
			
			spec_close(e){
				this.fold=true;
				this.btnspec=true
			},
			worker_close(e){
				this.workershow=false;
			},
			worker_ok(){
				this.workershow=false;
			},
			 async detail(goods_id,token){
				const res = await this.$myRequset({
					url:"/api",
					data:{
						operate:"Goods.get",
						token:token,
						goods_id:goods_id
					}					
				})
				
				let index = 0;
				var obj={}
				var States = {};
				for(obj in res.data.data.specs){
					
					States[index] = 0;
					
					index++;
				}
				
				this.States = States;
				console.log("详情")
				console.log(res)
				this.goodsdtail =res.data.data
				
					if(this.goodsdtail.unit==1){
						this.goodsdtail.unit="天"
					}else if(this.goodsdtail.unit==2){
						this.goodsdtail.unit="月"
					}else if(this.goodsdtail.unit==3){
						this.goodsdtail.unit="小时"
					}else if(this.goodsdtail.unit==10){
						this.goodsdtail.unit="个"
					}
					else if(this.goodsdtail.unit==11){
						this.goodsdtail.unit="千克"
					}
					else if(this.goodsdtail.unit==12){
						this.goodsdtail.unit="套"
					}
					
			
				
				this.goodsInfo = res.data.data;
				this.specs=res.data.data.specs
				this.specList = res.data.data.list
				
				console.log(this.States);
			},
			
			onShowDatePicker(type){
				this.type = type;
				this.showPicker = true;
				this.value = this[type];
				
			},
			onSelected(e){
				this.showPicker = false;
				if(e){
					this[this.type] = e.value;
					console.log('value=>'+e.value);
					this.datetime =e.value				
				}
			},
			//点击减号
			bindMinus(){
				var num = this.num;
				if(num>1){
					num--;
					this.num = num;
					this.ismaxNormal=true
				}else{
					this.isminNormal=false
				}
				
			},
			//点击加号
			bindPlus(){
				var num = this.num;
				if(num<9999){
					num++;
					this.num = num;
					this.isminNormal=true
				}else{
					
					this.ismaxNormal=false
				}
			},
			bindManual(e){
				var num = e.detail.value;
				this.num = num;
			}
		}
	}
</script>

<style lang="scss">
	.block{
		display: block;
	}
	.uni-rich-text img{
		max-width: 100% !important;
	}
	.span-tithz  button{
		background: #00c4bc;
		color: #fff;
		border: 1px solid #00c4bc;
		padding: 1rpx 10rpx;
		font-size: 24rpx;
	}
	.normal{
	    display: inline-block;
	} 
	.disabled{  
	    background: #f1f1f1!important; 
	}
	.hide{
		display: none!important;
	}
	.show{
		display: block!important;
	}
	.between{
		justify-content: space-between;	
	}
	
	.zhezhao{
		width: 100%;
		background: rgba(0,0,0,0.3);
		height: 100%;
		z-index: 98;
		position: fixed;
		top: 122rpx;
		bottom: 0;
	}
	.backWhite{
		background: #fff;
	}
	
	.odr_module{
		width: 100%;
		background: #fff;
		position: fixed;
		bottom: 172rpx;		
		border: 1px solid #f1f1f1;
		border-top-left-radius: 30rpx;
		border-top-right-radius: 30rpx;
		max-height: 1000rpx;
		overflow: hidden;
		z-index: 99;
		.date_btn{
			button{
				color: #8e8e8e;
				background: #fff;
				border: 1px dashed #f1f1f1;
				border-radius: 8rpx;
				height: 60rpx;
				line-height: 60rpx;
				font-size: 26rpx;
			}
			
			
		}
		.stepper{
			width:250rpx;
			display: flex;
			text{
				border:1px solid #23c1bb;
				color: #23c1bb;
				margin-left: 10rpx;				
				margin-right: 10rpx;
				width: 42rpx;
				height: 42rpx;
				display: inline-block;
				text-align: center;
				line-height: 42rpx;
				border-radius: 6rpx;
			}
			
			input{
				text-align: center;
				border: 1px solid #f1f1f1;
				color: #8e8e8e;
				width: 102rpx;
				height: 42rpx;
				border-radius: 6rpx;
			}
			
		}
		.cont_t{
			padding: 30rpx;
			.specs{
				display: block;
				padding: 0 20rpx;
			}
			.span-tit{
				padding: 20rpx 0;
				font-size: 26rpx;
			}
			.clr23c0ba{
				color: #23c0ba;
				font-size: 20rpx;
			}
			.spec{
				display: inline-block;
				margin-top: 10rpx;
				text{
					background: #fff;
					color: #8e8e8e;
					font-size: 26rpx;
					margin-right: 10rpx;
					padding: 6rpx 24rpx;
					border-radius: 20rpx;
					border: 1px solid #eeeeee;
				}
				.activeClass{
					background: #23c0bb;
					color: #ffffff;
					border: 1px solid #23c0bb;
				}
				.errorClass{
					background: #fff;
					color: #8e8e8e;
					border: 1px solid #eeeeee;
				}
			}
			.w21{
				width: 21rpx;
				height: 21rpx;
				padding-right: 10rpx;
			}
			.top_T{
				border-bottom: 1px solid #f1f1f1;
			}
			.md_title{
				font-size: 32rpx;
			}
		}
		
	}
	.w28{
		width: 28rpx;
		height: 28rpx;
		position: absolute;
		right: 30rpx;
		top: 30rpx;
	}
	.demo{
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    background: #ffffff;
	    font-size: 26rpx;
		width: 100%;
		text-align: center;
		position: fixed;
		top: 0;
	    .left{
	        float: left;
	        position: absolute;
	        width: max-content;
	        height: max-content;
	        top: 0;
	        bottom: 0;
	        left: 20rpx;
